html{
    min-height:100%;
    position:relative;
}
body {
  height:100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    overflow:hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
form{
  max-width:600px;
  padding:25px;
}
.formButtons{
  margin-top:15px;
}

.fabContainer {
  position: absolute;
  bottom: 15px;
  right: 15px;
}
.fabContainer .fab {
  margin-left: 5px;
}
.colunaBotoes{
  width:50px;
}
